import App, { Container } from 'next/app'
import Page from '../components/Page'
import { ApolloProvider } from 'react-apollo';
import withData from '../lib/withData';

// Mostly boilerplate from the documentation, the getInitialProps is for server side rendering

// getInitialProps is a special next.js lifecycle method which runs before render üëç

// pageProps gives you the appropriate page numbers

class MyApp extends App {
    static async getInitialProps({ Component, ctx}) {
        let pageProps = {};
        if(Component.getInitialProps) {
            pageProps = await Component.getInitialProps(ctx);
        }
        // this exposes the query to the user
        pageProps.query = ctx.query
        return { pageProps };
    }
    render() {
        const { Component, apollo, pageProps } = this.props;
    
    return (
        <Container>
            <ApolloProvider client={apollo}>
                <Page>
                    <Component {...pageProps} />
                </Page>
            </ApolloProvider>
        </Container>
        )
    }
}

export default withData(MyApp);